import React , {Component} from 'react'
import TodoItems from './todolist_components/app_todo_items'
import CE1 from '../files/1.pdf'
/*import CE2 from '../files/2.pdf'*/
import TM1 from '../files/3.pdf'
import TM2 from '../files/4.pdf'



class   todo_list extends Component{
    constructor(props){
        super(props)
        
        
        //creating values
        this.state = {
            items : []
        }
        
        
        // binding the function
        this.add_item = this.add_item.bind(this);
        this.deleteItem = this.deleteItem.bind(this);
        
        
    }
    
    add_item(e){
        e.preventDefault()
        if(this._inputElement.value !== ''){
            let newItem = {
                text : this._inputElement.value ,
                key : Date.now(),
              
                
            };
            
            
            this.setState((previousState) => {
            return {
                items : previousState.items.concat(newItem)
                }
            })
        }
        
        this._inputElement.value = ""
       
    }
    
    deleteItem(key){
        var filteredItems = this.state.items.filter(function(item){
            return (item.key !== key)
        })
        
        this.setState({
            items : filteredItems
        })
    }
    
    
    render(){
        
        
        return(
            <div className='row ' >
            
                <section className='col-md-6 top'>
                
                <ul className="list-group">
                    <li className="list-group-item text-light bg">Computer Engineering</li>

                    <li className="list-group-item "><p>Computer Aided Engineering Design</p>
                        <button className = 'btn bg text-light '><a className='text-light ' href={CE1} download>Download</a></button>    
                    </li>
                    
            {/*<li className="list-group-item"><p>Risk and Security Management</p><button className = 'btn bg text-light float-left'><a className='text-light' href={CE2} download>Download</a></button></li> */}

                </ul>
            
                <ul className="list-group pt-5">
            <li className="list-group-item text-light bg">Tourism Management</li>
            <li className="list-group-item"><p>Food and Beverages Cost Control</p><button className = 'btn bg text-light float-left'><a className='text-light' href={TM1} download>Download</a></button></li>
            <li className="list-group-item"><p>The Management of tourism</p><button className = 'btn bg text-light float-left'><a className='text-light' href={TM2} download>Download</a></button></li>
                  
                </ul>

            
                </section>
            
                <div className= 'todolist_main col-md-6 '>
                    <h5 className = 'header top'>Leave a reply</h5> 
                    
                    <form onSubmit = {this.add_item}>
                        <input ref = {(a) => this._inputElement = a} 
                        required/> 
                        <br/>
                        <button type = 'submit' className='customize_btn' >Post a comment</button>
                    </form>
                    <TodoItems entries = {this.state.items} delete = {this.deleteItem}/> 
                </div>
                
            </div>
        )
    }
}

export default todo_list