import React, {Component } from 'react'
import Footer from './app_footer'
import {AnimatedOnScroll} from "react-animated-css-onscroll";



class admission extends Component{
    
   
    
    render(){
        return(
            <div >
                
                
            
            
                <div className = 'container top '>
                <AnimatedOnScroll animationIn="bounceInLeft" animationOut="fadeOut">
                    <div>
                        <h1 className= 'highlight'>Admission</h1>
                    </div>
                </AnimatedOnScroll>
                <div className = 'admission_border top'>
                <h5 className=' highlight '>Entrance Qualification:</h5>
                <p>Must be at least a high school graduate (No entrance examination)</p>
                <h5 className='highlight '>Credentials to be submitted:</h5>
                <p>For Freshmen Students</p>
                <li>High school card or Form 138</li>
                <li>Form 137-A (High School Permanent Record)</li>
                <li>Good Moral Character</li>
                <li>2pcs 2 x 2 colored ID Pictures</li>
                <br/>
                <p>For Transferees</p>
                <li>Honorable Dismissal/Transfer Credentials</li>
                <li>Transcript of Records</li>
                <li>Course Description for assessment of subjects taken</li>
                <li>2pcs 2 x 2 colored ID Pictures</li>
                </div>
                </div>
                <Footer />
            </div>
        )
    }
}
export default admission

