import React , {Component} from 'react'
import shs from '../img/shs.jpg'
import Footer from './app_footer'
import {AnimatedOnScroll} from "react-animated-css-onscroll"

class home extends Component{
   
    
    form_handler(){
        let submit_form = document.forms['gform']
        submit_form.addEventListener('submit' , function(){
            
            submit_form.style.display = 'none'
            
            let success = document.querySelector('#sent').children
            Array.from(success).forEach(function(i){
                i.style.display = 'block'
            })
            
        })
        
        
    }
    
   
    render(){
       
    
        
        return(
            <div >
            
            <div className = 'container top'>
            <div className = 'row'>
                <div className = 'col-md-6 ima'>
                    <img src = {shs} alt = 'shs' className='imahe'/>
                </div>
                <div className = 'col-md-6'>
    
                            
<AnimatedOnScroll animationIn="fadeIn" animationOut="fadeOut">
    <h1 className='highlight title' >Access Computer College</h1>



            <br/>        
            <p>Skills For Living , Knowledge For Life With programs that integrate liberal arts with professional studies and career preperation, Access Computer, College inspires it's students to create for themselve's a future that is beyond their imagining</p>
     </AnimatedOnScroll>              
            </div>
            
                
                
                
            
            
            
            
            
            
            </div>
            
            <div className = 'row top'>
                    
                    <div className = 'col-md-6'>
            
                               
                                         <div className = 'card'>

                                        <div className = 'card-header bg text-light'>
                                            Contact Form
                                        </div>
                                        <div className = 'card-body'>
                                        <div id = 'sent'>
                                            <p className = 'highlight' style = {{ display : 'none' }} >Message Sent</p>
           
                                            <p style = {{ display : 'none' }}>Thank you for contacting us! We will get back to you soon in the email you provided</p>
                                           
                                            
                                        </div>
            
                                        <form id="gform" target = '_blank' method="POST" className="pure-form pure-form-stacked" data-email="from_email@example.com"
                                        action="https://script.google.com/macros/s/AKfycby-8Q5nFxqEC3EkvvYn9EABFsUFnochZTKDHkNGeyCWjNe0RT98/exec" >

                                        <label htmlFor="name">* Name</label><br/>
                                        <input id="name" name="name"  />

                                        <br/>


                                        <label htmlFor="message">* Message </label><br/>
                                        <textarea id="message" name="message" rows="5" cols = '40'
                                                  ></textarea>

                                        <br />

                                        <label htmlFor="email">* Your Email Address</label><br/>
                                        <input id="email" name="email" type="email" 
                                               required  />


                                        <br/>
                                        <br/>
                                        <button className="btn bg text-light    "  onClick = { this.form_handler }>
                                            <i className="fa fa-paper-plane" ></i>&nbsp;Send 
                                        </button>
                                       
                                        </form>
                                        </div>
                                        </div>




                                <script data-cfasync="false" type="text/javascript"
                                        src="https://cdn.rawgit.com/dwyl/learn-to-send-email-via-google-script-html-no-server/master/form-submission-handler.js"></script>
            
                                
                        </div>
                    <div className = 'col-md-6 note'>
            
            <AnimatedOnScroll animationIn="bounceInRight"  >
     <div className="card ">
                            <div className="card-header bg text-light">
                                Contact us

                            </div>

                            <div className="card-body">
                                <p>* We would love to hear from you!</p>
                            <p className="card-title"> * Please use the Contact Form to send us a message.</p>
                            </div>
                        </div>

</AnimatedOnScroll>
                       
                        <br/>


                        </div>
            
                        
            
            
            </div>
            
            
                    
            
            </div>
            
            
            
            <Footer />
            
            </div>
            
        )
    }
}
export default home