import React , {Component} from 'react'
import Footer from './app_footer'
import TodoList from './todo_list'


class blog extends Component{
    
    render(){
        
        return(
            <div >
               <div  className = 'container top'>
                    <TodoList />
                    
                </div>
                <Footer />
            </div>
        )
    }
}

export default blog