import React , { Component} from 'react'
import Navbar from './components/app_navbar'
import Home from './components/app_home'
import Campuses from './components/app_campuses'
import Admission from './components/app_admission'
import About from './components/app_about'
import Courses from './components/app_courses'
import Blog from './components/app_blog'
import Mv from './components/app_missionvission'
import {BrowserRouter , Route} from 'react-router-dom'

class App extends Component{
    
    constructor(){
        super()
        this.state = {
           author : 'Christian Jerr T. Buarao'
        }
    }
   
    
    
    render(){
        
        
        
        return(
            <BrowserRouter>
            <div>
                <Navbar />
               <Route exact path = '/accessofficial' component = {Home} />
               <Route exact path = '/campuses' component = {Campuses} />
               <Route exact path = '/courses' component = {Courses} />
               <Route exact path = '/admission' component = {Admission} />
               <Route exact path = '/about' component = {About} />
               <Route exact path = '/mission_vission' component = {Mv} />
               <Route exact path = '/blog' component = {Blog} />
            </div>
            </BrowserRouter>
        )
    }
}

export default App






