import React , {Component} from 'react'
import access from '../img/access.png'
import {Link} from 'react-router-dom'

class navbar extends Component{
    constructor(){
        super()
        this.state = {
            
        }
    }
        
    change_navbar(){
      if(window.innerWidth < 768){
            
            document.querySelector('nav').classList.remove('bg-custom')
            document.querySelector('nav').classList.remove('navbar-dark')
            document.querySelector('nav').classList.add('navbar-light')
            document.querySelector('nav').classList.add('bg-light')
         }
        
        /*
       
       */
       
    }
    
   
    render(){
        let logo ={
        width : '200px' ,
        
        }
        
        
        
       
        return(
            <div onLoad = {this.change_navbar}>
                <nav className="navbar navbar-expand-lg  navbar fixed-top  bg-custom navbar-light get">
                    
                  <Link to= '/accessofficial' className="navbar-brand"><img src = {access} alt = 'logo' style = {logo}/></Link>
                  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                  </button>

                  <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item active">
                        <Link to = '/accessofficial' className="nav-link" >Home</Link>
                      </li>
                      <li className="nav-item ">
                        <Link to = "/campuses" className="nav-link" >Campuses</Link>
                      </li>
                      <li className="nav-item">
                        <Link to = '/courses' className="nav-link" >Courses</Link>
                      </li>
                      <li className="nav-item">
                        <Link to = '/admission' className="nav-link " >Admission</Link>
                      </li>
                        <li className="nav-item">
                        <Link to = '/about' className="nav-link" >About us</Link>
                      </li>
                      <li className="nav-item">
                        <Link to = '/mission_vission' className="nav-link" >Mission / Vision</Link>
                      </li>
                        <li className="nav-item">
                        <Link to = '/blog' className="nav-link" >Files</Link>
                      </li>
                    </ul>
                    
                  </div>
                </nav>

            
            </div>
        )
    }
}
export default navbar