import React , {Component} from 'react'
import Logo from '../img/banner.png'
import Footer from './app_footer'
import {AnimatedOnScroll} from "react-animated-css-onscroll";


class mv extends Component{
    constructor(){
        super()
        this.state = {
            
        }
    }
    
    
    
    render(){
       
       
        
        return(
            
            <div >
                
                <div className = 'container top text-justify'>
            <div className = 'row'>
                <div className = 'col-md-6'>
 
<AnimatedOnScroll animationIn="bounceInLeft" animationOut="fadeOut">
    <div>
         <h1 className = 'highlight'>Mission / Vision</h1>
    </div>
</AnimatedOnScroll>
                       
                        <h5 className = 'highlight top '>Mission</h5>
                       <p> To educate its students by means of curriculum grounded on practical application and technological integration along side the philosophy of the total academic, co and extra curricular experiences. We strive to prepare competent graduates aimed at changing the nation through competency and values, fulfilling its duty to our faculty, employees, students and society as true educational institution.</p>
                        <br/>
                        <br/>
                        <h5 className='highlight'>Vision</h5>
                        <p>To be the nation's premiere choice of school for tertiary level education and technical vocational training.</p>
                </div>
                <div className = 'col-md-6 banner_container top'>
            <AnimatedOnScroll animationIn="jello" animationOut="fadeOut">
    <div>
        <img  src = {Logo} alt =  'logo' id = 'banner'/>
    </div>
</AnimatedOnScroll>
                    
                </div>
            </div>
                </div>
            <Footer />
            </div>
          
        )
    }
}
export default mv